import React from "react";
import { Container, Row, Col, Accordion } from "react-bootstrap";

const Faq = ({ faqData }) => {
  return (
    <div className="faq_outline">
      <Container>
        <Row className="faq_row">
          


          <Col xxl={12} xl={12} lg={12} md={12} sm={12} className="p-lg-0">
            <div className="faq_rhs">
              <h2 className="faq_title">
                Resolve your unanswered queries effortlessly
              </h2>
              <div className="faq_acc_wrapper">
                <Accordion>
                  {faqData.map((item, index) => (
                    <Accordion.Item key={index} eventKey={index.toString()}>
                      <Accordion.Header>
                        <div>
                        {index + 1}.
                        </div>
                        <div>
                        {item.question}
                        </div>
                         </Accordion.Header>
                      <Accordion.Body>{item.answer}</Accordion.Body>
                    </Accordion.Item>
                  ))}
                </Accordion>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Faq;
